.category {
  background-color: #f2f4f7;
  padding: 12px 18px;
  border-radius: 100px;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.categoryValue {
  margin-right: 4px;
}

.categoryIcon {
  width: 20px;
  height: 20px;
  margin-right: 8px;
}
