.button {
  padding: 16px 60px;
  border-radius: 200px;
  background-color: #ffffff;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  display: inline-block;
  color: #101828;
  border: 1px solid #475467;
  text-decoration: none;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.005em;
  min-width: 173px;
}

.button:hover {
  border: 1px solid #e44848;
}
